.moto-transport {
  padding-top: 32px;
}
.moto-transport__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 24px;
}
@media (max-width: 1024px) {
  .moto-transport__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 480px) {
  .moto-transport__list {
    grid-gap: 16px;
  }
}
@media (max-width: 375px) {
  .moto-transport__list {
    grid-gap: 8px;
  }
}
@media (max-width: 320px) {
  .moto-transport__list {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 8px;
  }
}
.moto-transport__item {
  cursor: pointer;
  display: block;
  background: var(--color-bg_gray);
  border: 1px solid transparent;
  padding: 24px 0 24px 24px;
  border-radius: 8px;
  text-decoration: none;
  transition: all 0.4s ease-in-out;
}
.moto-transport__item:hover {
  background: #fff;
  border: 1px solid #f3f3f3;
  box-shadow: 0px 24px 32px rgba(45, 49, 65, 0.24);
  transform: translateY(-16px);
}
.moto-transport__item:hover .moto-transport__item--arrow {
  width: 52px;
  height: 52px;
  background: var(--color-primary);
  color: #fff;
}
.moto-transport__item--img {
  width: 100%;
  height: 240px;
  margin-bottom: 32px;
  display: flex;
  align-items: center;
}
.moto-transport__item--img img {
  flex-shrink: 0;
  width: auto;
  max-height: 240px;
  position: relative;
}
.moto-transport__item--footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 24px;
}
.moto-transport__item--name {
  color: var(--color-body);
  font-size: 24px;
  font-weight: 400;
  margin: 0;
}
.moto-transport__item--arrow {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border-radius: 50%;
  color: var(--color-primary);
  transition: all 0.3s ease-in-out;
}

@media (max-width: 1024px) {
  .moto-transport__item--img {
    justify-content: center;
  }
}
@media (max-width: 600px) {
  .moto-transport__item {
    padding: 24px;
  }
  .moto-transport__item:hover {
    background: none;
    border: none;
    box-shadow: none;
    transform: none;
  }
  .moto-transport__item--footer {
    padding-right: 0;
  }
  .moto-transport__item--arrow {
    display: none;
  }
  .moto-transport__item--img {
    height: 140px;
    margin-bottom: 16px;
  }
  .moto-transport__item--name {
    font-size: 18px;
  }
}
@media (max-width: 480px) {
  .moto-transport {
    padding-top: 0;
  }
  .moto-transport__item {
    padding: 16px;
  }
}
@media (max-width: 375px) {
  .moto-transport__item {
    padding: 8px;
    border-radius: 4px;
  }
}
@media (max-width: 320px) {
  .moto-transport__item {
    padding: 16px;
  }
  .moto-transport__item--img {
    height: 200px;
  }
  .moto-transport__item--img img {
    max-height: inherit;
  }
}